#image-project-image {
    height: 100%;
    backface-visibility: hidden;
    transition: all .3s ease-in;
}

.container-member {
    position: relative;

    &:hover #image-project-caption {
        opacity: 1;
        transform: translate(-50%, -20%);
    }

    &:hover #image-project-caption__smart {
        opacity: 1;
        transform: translate(-50%, -20%);
    }

    &:hover #image-project-image {
        filter: blur(6px);
    }
}

#image-project-caption {
    width: 100%;
    position: absolute;
    left: 50%;
    top: 40%;
    transform: translate(-50%, 300%);
    color: white;
    text-transform: uppercase;
    font-size: 1.7rem;
    text-align: center;
    opacity: 0;
    transition: all .6s ease-out;
    backface-visibility: hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;

    &__smart {
        width: 100%;
        position: absolute;
        left: 50%;
        top: 30%;
        transform: translate(-50%, 300%);
        color: white;
        text-transform: uppercase;
        font-size: 1.7rem;
        text-align: center;
        opacity: 0;
        transition: all .6s ease-out;
        backface-visibility: hidden;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
    }
}

#Link-text {
    cursor: pointer;
    font-size: 20px;
    border: 3px solid rgba(208, 214, 207, 0.592);
    padding: 5px;
    transition: 0.4s ease-in-out;
    margin-bottom: 5px;

    &:hover {
        background: rgb(241, 240, 240);
        color: rgba(44, 41, 41, 0.884);
    }
}
